<template>
    <div id="youridfortheformgoeshere">
    <v-text-field label="Username/Email" v-model="userEmail"/>
    <v-text-field type="password" label="Password" v-model="userPassword"/>
    <v-container v-show="isLoggedIn === false">
        <v-row justify="end">
            <v-tooltip bottom>
                <template v-slot:activator="{ on }">
                    <v-btn color="secondary" v-on:click="signUpButtonHandler" v-on="on">SignUp</v-btn>
                </template>
                <span>This button signs you up for an access account using the entered information above</span>
            </v-tooltip>
            <v-tooltip bottom>
                <template v-slot:activator="{ on }">
                    <v-btn color="secondary" v-on:click="signInButtonHandler" v-on="on">SignIn</v-btn>
                </template>
                <span>This button signs you into your account using the entered information above</span>
            </v-tooltip>
            
            
        </v-row>
    </v-container>
        <v-snackbar
        v-model="snackbar"
        >
            {{ text }}
            <v-btn
                color="primary"
                text
                @click="snackbar = false;"
            >
                Close
            </v-btn>
        </v-snackbar>
    </div>
</template>

<script>
import { AppAUTH } from "../db-init.js";

export default {
    data: () => ({
        userEmail: "",
        userPassword: "",
        isLoggedIn: false,
        snackbar: false,
        text: ""
    }),
    methods: {
        signUpButtonHandler()
        {
            AppAUTH.createUserWithEmailAndPassword(this.userEmail, this.userPassword)
                .then((u) => {
                    this.text ="User created with UID " + u.user.uid;
                    this.snackbar=true;
                    this.$router.push({ path: "/budget" });
                })
                .catch((err) => {
                    this.text = err;
                    this.snackbar=true;
                });
        },
        signInButtonHandler()
        {
            AppAUTH.signInWithEmailAndPassword(this.userEmail, this.userPassword)
            .then((u) => {
                this.text ="You logged in as " + u.user.email;
                this.snackbar=true;
                this.$router.push({ path: "/budget" });
            })
            .catch((err) => {
                this.text = err;
                this.snackbar=true;
            });
        }
    },
    mounted(){
        AppAUTH.onAuthStateChanged((u) => {
            if (u == null) this.isLoggedIn = false;
            else this.isLoggedIn = true;
        });
    }
}
</script>

<style>
#youridfortheformgoeshere{
    width: 50vw;
    margin: 20px;
    padding-top:30px;
    padding-bottom: 30px;
    padding-left:15px;
    padding-right:15px;
    border: 2px solid rgb(112, 112, 112);
    border-radius: 10px;
}

</style>