<template>
  <v-container fluid> 
    <h2>Austin DeBoer</h2>
    <v-row align="start" justify="space-around">
    <v-col>
        <div id = "input">
            <v-row justify="center">
                <v-date-picker v-model="expenseDate" />
            </v-row>
            <v-text-field label="Description" type="text" v-model="expenseDesc" />
            <v-text-field label="Amount" type="text" v-model="expenseAmt" />
            <v-select
                v-model = "expenseCat"
                v-bind:items="categories"
                label="Category"
            ></v-select> 
            <v-tooltip bottom>
                <template v-slot:activator="{ on }">
                    <v-btn color="Secondary" @click="yourButtonHandler" v-on="on">Add</v-btn>
                </template>
                <span>This button adds entered feilds into the database</span>
            </v-tooltip>
        </div>
    </v-col>

    <v-col>
        <v-app>
            <template>
                <v-simple-table>
                    <template v-slot:default>
                        <thead>   
                            <tr id="head">
                                <th>Date</th>
                                <th>Description</th>
                                <th>Amount</th>
                                <th>Category</th>
                                <td>
                                    <v-tooltip bottom>
                                        <template v-slot:activator="{ on }">
                                            <v-btn color="primary" id="rem" @click="removeHandler" v-bind:disabled="userSelections.length == 0" v-on="on">Remove Items</v-btn>
                                        </template>
                                        <span>This button removes selected rows</span>
                                    </v-tooltip>
                                    
                                
                                </td>
                            </tr>
                        </thead>
                        <tbody>
                            <tr v-for="(myExpense,pos) in myExpense" :key="pos">
                                <td>{{myExpense.date}}</td>
                                <td>{{myExpense.description}}</td>
                                <td>{{myExpense.category}}</td>
                                <td class="rightAlign">${{myExpense.amount}}</td>
                                <td>
                                    <input  type="checkbox" 
                                            v-bind:id="myExpense.mykey" 
                                            v-on:change="selectionHandler"
                                    >
                                </td>
                            </tr>
                            <tr>
                                <td colspan="3" class="rightAlign"><b>Total: </b></td>
                                <td class="rightAlign">${{ totalExpense.toFixed(2) }}</td>
                            </tr>
                        </tbody>
                    </template>
                </v-simple-table>
            </template>
        </v-app>
    </v-col>
    <v-col>
        <div id="chart">
            <pie-chart :data="[['Entertainment', amtEntertainment], ['Gas', amtGas], ['Food', amtFood], ['Bill', amtBill], ['Work', amtWork]]"></pie-chart>
        </div>
        </v-col>
    </v-row>
  </v-container>
</template>

<script>
import { AppDB } from "../db-init.js";

export default {
  data: () => ({
    myExpense:[],
    userSelections:[],
    categories: [`Entertainment`, `Gas`, `Food`, `Bill`, `Work`],
    totalExpense: 0,
    expenseDate: "",
    expenseDesc: "",
    expenseAmt: 0,
    expenseCat: "",
    amtEntertainment: 0,
    amtGas: 0,
    amtFood: 0,
    amtBill: 0,
    amtWork: 0,
  }),
  methods: {
        yourButtonHandler() {
            AppDB.ref("budget")
                .push()
                .set({
                    date: this.expenseDate,
                    description: this.expenseDesc,
                    amount: this.expenseAmt,
                    category: this.expenseCat
                });
        },
        fbAddHandler(snapshot) {
            const item = snapshot.val();
            this.myExpense.push({ ...item, mykey: snapshot.key });

            // accumulate the total
            this.totalExpense += parseFloat(item.amount);
            if(item.category == "Entertainment") this.amtEntertainment += parseFloat(item.amount);
            else if(item.category == "Gas") this.amtGas += parseFloat(item.amount);
            else if(item.category == "Food") this.amtFood += parseFloat(item.amount);
            else if(item.category == "Bill") this.amtBill += parseFloat(item.amount);
            else if(item.category == "Work") this.amtWork += parseFloat(item.amount);
        },
        selectionHandler (changeEvent) {
            const whichKey = changeEvent.target.id;
            if (changeEvent.target.checked) {
            this.userSelections.push(whichKey);
            } 
            else {
            this.userSelections = this.userSelections.filter((s) => s != whichKey);
            }
        },
        removeHandler () {
            this.userSelections.forEach((victimKey) => {
                //this.totalExpense -= victimKey.amount;
                this.myExpense.forEach((a) => {
                    if(a.mykey == victimKey) this.totalExpense -= a.amount;
                    if(a.mykey == victimKey) {
                        if(a.category == "Entertainment") this.amtEntertainment -= a.amount;
                        else if(a.category == "Gas") this.amtGas -= a.amount;
                        else if(a.category == "Food") this.amtFood -= a.amount;
                        else if(a.category == "Bill") this.amtBill -= a.amount;
                        else if(a.category == "Work") this.amtWork -= a.amount;
                    }
                });
                AppDB.ref('budget').child(victimKey).remove();
            })
            this.userSelections = [];
        },
        fbRemoveListener (snapshot){
             /* snapshot.key will hold the key of the item being REMOVED */
            this.myExpense = this.myExpense.filter(z => z.mykey != snapshot.key);
        }
    },
    mounted(){
        AppDB.ref("budget").on("child_added", this.fbAddHandler);
        AppDB.ref("budget").on("child_removed", this.fbRemoveListener);
    },
    beforeDestroy() {
        AppDB.ref("budget").off("child_added", this.fbAddHandler);
        AppDB.ref("budget").off("child_removed", this.fbRemoveListener);
    },
};
</script>
